.container-steps {
  margin: 0 -80px;

  @media (max-width: 1300px) {
    margin: 0 -60px;
  }

  @media (max-width: 740px) {
    margin: 0 -40px;
  }

  @media (max-width: 540px) {
    margin: 0 -20px;
  }

  @media (max-width: 360px) {
    margin: 0 -15px;
  }
}


.steps {
  background-color: #171717;
  color: #F5F5F5;
  padding: 60px 100px;

  @media (max-width: 1300px) {
    padding: 50px 60px;
  }

  @media (max-width: 740px) {
    padding: 40px 40px;
  }

  @media (max-width: 540px) {
    padding: 30px 20px;
  }

  @media (max-width: 360px) {
    padding: 25px 15px;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    gap: 40px;

    @media (max-width: 1300px) {
      gap: 30px;
    }

    @media (max-width: 740px) {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__title {
    max-width: 50%;

    @media (max-width: 740px) {
      max-width: 100%;
    }
  }

  &__list {
    max-width: 503px;
    counter-reset: my-counter;
    list-style: none;
    padding-left: 0;
    flex: 0 0 503px; // Фиксированная ширина

    @media (max-width: 1300px) {
      flex: 0 0 55%;
      max-width: 55%;
      padding-right: 30px;
    }

    @media (max-width: 740px) {
      flex: 0 0 100%;
      max-width: 100%;
      padding-right: 0;
      width: 100%;
    }
  }

  &__img-container {
    flex: 1;
    overflow: hidden;

    @media (max-width: 1300px) {
      flex: 0 0 45%;
    }

    @media (max-width: 740px) {
      width: 100%;
      margin-top: 30px;
    }

    @media (max-width: 540px) {
      margin-top: 20px;
    }

    @media (max-width: 360px) {
      margin-top: 15px;
    }
  }

  &__img {
    width: 125%;
    height: auto;
    display: block;
    object-fit: cover;
    object-position: center;

    @media (max-width: 1300px) {
      height: 100%;
      min-height: 500px;
    }

    @media (max-width: 740px) {
      width: calc(100% + 80px);
      margin-left: -40px;
      margin-right: -40px;
      height: 320px;
      min-height: auto;
    }

    @media (max-width: 540px) {
      width: calc(100% + 40px);
      margin-left: -20px;
      margin-right: -20px;
      height: 240px;
    }

    @media (max-width: 360px) {
      width: calc(100% + 30px);
      margin-left: -15px;
      margin-right: -15px;
      height: 200px;
    }
  }

  &__item {
    counter-increment: my-counter;
    position: relative;
    padding-left: 57px;
    padding-bottom: 40px;

    @media (max-width: 1300px) {
      padding-left: 50px;
      padding-bottom: 35px;
    }

    @media (max-width: 740px) {
      padding-left: 45px;
      padding-bottom: 30px;
    }

    @media (max-width: 540px) {
      padding-left: 40px;
      padding-bottom: 25px;
    }

    @media (max-width: 360px) {
      padding-left: 35px;
      padding-bottom: 20px;
      margin-bottom: 16px;
    }

    &:last-child {
      padding-bottom: 0;

      @media (max-width: 360px) {
        margin-bottom: 0;
      }
    }

    &:last-child:after {
      display: none;
    }

    &:before {
      content: counter(my-counter);
      position: absolute;
      width: 40px;
      height: 40px;
      border: 1px solid #f5f5f5;
      font-size: 15px;
      font-weight: 600;
      font-family: "Monserrat", sans-serif;
      display: flex;
      justify-content: center;
      align-items: center;
      left: 0;

      @media (max-width: 740px) {
        width: 36px;
        height: 36px;
        font-size: 14px;
      }

      @media (max-width: 540px) {
        width: 32px;
        height: 32px;
        font-size: 13px;
      }

      @media (max-width: 360px) {
        width: 28px;
        height: 28px;
        font-size: 12px;
      }
    }

    &:after {
      content: '';
      position: absolute;
      width: 1px;
      background-color: #f5f5f5;
      left: 20px;
      top: 48px;
      bottom: 8px;

      @media (max-width: 740px) {
        left: 18px;
        top: 44px;
      }

      @media (max-width: 540px) {
        left: 16px;
        top: 40px;
      }

      @media (max-width: 360px) {
        left: 14px;
        top: 36px;
      }
    }

    &-title {
      font-weight: 300;
      font-size: 24px;
      color: #f5f5f5;
      margin-bottom: 12px;
      line-height: 1.16;
      padding-top: 6px;

      @media (max-width: 1300px) {
        font-size: 22px;
      }

      @media (max-width: 740px) {
        font-size: 20px;
        margin-bottom: 10px;
      }

      @media (max-width: 540px) {
        font-size: 18px;
        margin-bottom: 8px;
      }

      @media (max-width: 360px) {
        font-size: 16px;
        margin-bottom: 6px;
      }
    }

    &-text {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: #f5f5f5;
      opacity: 0.8;

      @media (max-width: 1300px) {
        font-size: 15px;
      }

      @media (max-width: 740px) {
        font-size: 14px;
      }

      @media (max-width: 540px) {
        font-size: 13px;
        line-height: 140%;
      }

      @media (max-width: 360px) {
        font-size: 12px;
      }
    }
  }
}

/* ===== 1300px ===== */
@media (max-width: 1300px) {
  .steps {
    display: block;
  }
}

/* ===== 740px ===== */
@media (max-width: 740px) {
  .steps__content {
    flex-direction: column;
  }
}