.catalog {
  padding: 150px 0;


  &__title {
    max-width: 556px;
    width: 100%;
  }

  &__content {
    display: flex;
    gap: 50px;
    align-items: center;
    margin-bottom: 150px;
  }

  &__form {
    max-width: 557px;


    &-title {
      font-weight: 300;
      font-size: 28px;
      text-transform: uppercase;
      color: #2b2b2b;
      max-width: 430px;
      margin-bottom: 20px;
      line-height: 1.16;


    }

    &-text {
      max-width: 430px;
      font-weight: 300;
      font-size: 18px;
      text-transform: uppercase;
      color: #2b2b2b;
      margin-bottom: 50px;
    }

    &-input {
      font-weight: 100;
      font-size: 24px;
      color: #2b2b2b;
      display: inline-block;
      padding: 20px;
      border: 1px solid #2b2b2b;
      margin-bottom: 30px;
      width: 100%;

    }

    &-label {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-bottom: 50px;
    }

    &-checkbox {
      width: 17px;
      height: 17px;
      accent-color: #171717;
      font-weight: 300;
      font-size: 14px;
      line-height: 146%;
      color: #2b2b2b;
    }

    &-btn {
      padding: 20px 54px;
      color: #f5f5f5;
      background-color: #171717;
      font-size: 18px;
      line-height: 1.16;
      text-transform: uppercase;
      font-weight: 300;
    }

  }
}

.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;


  & img {
    max-width: 100%;
  }
}


@media (max-width: 1300px) {

  .catalog {
    padding: 100px 0;
  }
  .catalog__content {
    display: block;

    & img {
      width: 100%;

    }
    .catalog__form-title {
      margin-top: 50px;

    }
  }
}



@media (min-width: 740px) and (max-width: 1000px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  .catalog {
    padding: 50px 0;

    & img {
    }
  }

  .catalog__content {
    margin-bottom: 50px;
  }
  .catalog__form-title {
    margin-top: 50px;

  }

  .catalog__form {
    max-width: 100%;
  }

}
