.whitespace-pre-line {
  white-space: pre-line;
}

.footer {
  background-color: #171717;
  padding: 100px 0;
  color: #f5f5f5;

  &__copyright {
    display: flex;
    gap: 6px;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.16;
  }

  &__inner {
    margin-bottom: 10px;
    display: flex;
    align-items: end;
    gap: 100px;
    padding-right: 174px;
  }

  &__logo {
    margin-right: auto;
  }

  &__nav {
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    text-transform: uppercase;
    text-align: center;
    color: #f5f5f5;
  }

  &__contact {
    font-weight: 300;
    font-size: 14px;
    color: #f5f5f5;
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.16;

    &-time {
      padding-right: 23px;
    }

    &-link {
      display: flex;
      gap: 10px;


    }
  }

  &__social {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    gap: 15px;
    transform: translateY(20px);

    &-link {
      display: flex;
      gap: 10px;
      line-height: 1.16;
    }
  }
}


@media (max-width: 1940px) {
  .footer__nav {


  }
  .footer__inner {
    padding-right: 90px;
  }
}


@media (max-width: 1000px) {
  .footer {
    padding: 60px 0;

    .container {
      padding-left: 20px;
      padding-right: 20px;
      max-width: 100%;
      margin: 0 auto;
    }

    &__inner {
      flex-direction: column;
      align-items: center; // Центрируем все элементы
      gap: 40px;
      padding-right: 0;
      padding-left: 0;
      margin-bottom: 40px;
      width: 100%;
      text-align: center;
    }

    &__logo {
      margin-right: 0;
      margin-bottom: 20px;
      justify-content: center;

      img {
        width: 180px;
        height: auto;
      }
    }

    &__nav {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center; // Центрируем навигацию
      text-align: center;
      gap: 20px;
      width: 100%;
      font-size: 14px;

      &:first-of-type {
        order: 2;
      }

      &:last-of-type {
        order: 3;
      }
    }

    &__nav-item {
      flex: 0 0 calc(50% - 10px);
      margin-bottom: 15px;
      text-align: center;
      display: flex;
      justify-content: center;

      a {
        text-align: center;
      }
    }

    &__contact {
      order: 4;
      width: 100%;
      gap: 15px;
      margin-top: 20px;
      text-align: center;

      &-time {
        padding-right: 0;
        text-align: center;
      }

      &-link {
        align-items: center;
        justify-content: center;
        text-align: center;
        flex-wrap: wrap;
      }
    }

    &__social {
      order: 5;
      width: 100%;
      transform: none;
      margin-top: 30px;
      gap: 15px;
      text-align: center;

      &-link {
        align-items: center;
        justify-content: center;
        text-align: center;
      }
    }

    &__copyright {
      justify-content: center;
      text-align: center;
      font-size: 12px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 40px 0;

    &__inner {
      gap: 30px;
      margin-bottom: 30px;
      padding-left: 15px;
      padding-right: 15px;
    }

    &__nav {
      font-size: 13px;
      gap: 15px;
      justify-content: center;
    }

    &__nav-item {
      flex: 0 0 100%;
      justify-content: center;
    }

    &__contact {
      &-link {
        font-size: 13px;
        justify-content: center;

        img {
          width: 16px;
          height: 16px;
        }
      }
    }

    &__social {
      &-link {
        font-size: 13px;
        justify-content: center;

        img {
          width: 16px;
          height: 16px;
        }
      }
    }

    &__copyright {
      font-size: 11px;
      justify-content: center;

      img {
        width: 12px;
        height: 12px;
      }
    }
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 30px 0;

    .container {
      padding-left: 15px;
      padding-right: 15px;
    }

    &__inner {
      gap: 25px;
      padding-left: 10px;
      padding-right: 10px;
    }

    &__logo img {
      width: 140px;
    }

    &__nav {
      font-size: 12px;
      gap: 12px;
      justify-content: center;
    }

    &__contact {
      gap: 12px;

      &-link {
        gap: 8px;
        font-size: 12px;
        justify-content: center;
        text-align: center;
      }
    }

    &__social {
      gap: 12px;

      &-link {
        gap: 8px;
        font-size: 12px;
        justify-content: center;
        text-align: center;
      }
    }
  }
}

// Для очень маленьких экранов
@media (max-width: 360px) {
  .footer {
    &__inner {
      gap: 20px;
      padding-left: 10px;
      padding-right: 10px;
    }

    &__nav {
      flex-direction: column;
      gap: 10px;
      align-items: center;
    }

    &__nav-item {
      flex: 0 0 auto;
      width: 100%;
      text-align: center;
    }

    &__contact-link,
    &__social-link {
      font-size: 11px;
      text-align: center;
      justify-content: center;
    }
  }
}

// Исправление для контейнера чтобы не было скролла
.container {
  max-width: 100%;
  overflow-x: hidden;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}