.request {
  padding: 150px;

  &__inner {
    background-image: url('../../assets/img/reqbg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    padding: 184px 0 ;
    display: flex;
    justify-content: center;

    &-form {
      padding: 75px 115px;
      background-color: rgba(23,23,23,.85);
      max-width: 1000px;
      margin: 0 auto;

    }
  }

  &__form {
    display: flex;


    &-title {
      font-weight: 300;
      font-size: 42px;
      text-transform: uppercase;
      color: #f5f5f5;

    }

    &-btn {
      background-color: #f5f5f5;
      text-transform: uppercase;
      font-size: 18px;
      font-weight: 300;
      line-height: 1.16;
      padding: 20px 51px;
      width: 265px;
    }
    &-input {
      font-weight: 100;
      font-size: 18px;
      color: #2b2b2b;
      background-color: #D9D9D9;
      display: inline-block;
      padding: 20px;
      width: 100%;

    }

  }
}

@media (max-width: 740px) {

  .request__inner-form {
    padding: 50px 10px;
  }
  .request__form {
    display: flex;
    flex-direction: column;
    gap:20px;


    &-btn {
      width: 100%;
    }
  }
}